import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)
const BaseView = () => import("@/layout/baseView")
const mainView = () => import("@/layout/mainView")
const resultView = () => import("@/layout/resultView")
const submitView = () => import("@/layout/submitView")

export const routes = [
    {
        path: '',
        component: BaseView,
        redirect: 'mainView',
        children: [
            {
                path: 'mainView',
                name: 'mainView',
                component: mainView,
            },
            {
                name: 'result',
                path: 'result',
                component: resultView,
            },
            {
                name: 'submitView',
                path: 'submitView',
                component: submitView,
            }
        ]
    },


]

// 3. 创建 router 实例，然后传 `routes` 配置
// 你还可以传别的配置参数, 不过先这么简单着吧。
const router = new VueRouter({
    routes // (缩写) 相当于 routes: routes
})
export default router;